.App{
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
body{
  margin: 0;
  padding: 0;
}
.board{
  width: 500px;
  height: 500px;
  background-color: aqua;
  border: 2px solid black;
  display: flex;
  flex-direction: column;
}
.row{
  flex: 33%;
  display: flex;
  flex-direction: row;
}
.square{
  flex:33%;
  height: 100%;
  border: 2px solid black;
  cursor: pointer;
  display: grid;
  place-items:center;
  font-size: 80px;
  color: black;
  font-family:Georgia, 'Times New Roman', Times, serif;
}
.square:active{
  background-color: yellow;
}